import React from 'react'
import {VariablesTable} from '../../components'
import {MethodsTable} from '../../components'
import { useContext } from 'react'
import {DataContext} from '../../context/DataContext'
import './TableData.css'

function TableData() {
    const [datareceived,setdatareceived] = useContext(DataContext)
    const arr = datareceived[0]
    const ext = datareceived[1]
   
    return (
        <div className="whole-table">
    
            <div  className="divide-dashed">
            {arr.map(a => (

            <div className="tableinstance">
            <div className="AB">
            {a[3] !== ""?
            (<div class="w-full flex items-center justify-center bg-blue-500 text-white text-sm font-bold px-4 py-3" role="alert">
            <p>{a[0]} extends {a[3]}</p>
          </div>
            )
            :
            (<div class="w-full flex items-center justify-center bg-blue-500 text-white text-sm font-bold px-4 py-3" role="alert">
            <p>{a[0]}</p>
          </div>)
            }
            </div>

            {a[4].length !== 0?(
            <div class="BA">
            {ext === '.cpp'?
            (<div class="mt-2 flex items-center justify-center bg-blue-500 text-white text-sm font-bold px-4 py-3" role="alert">
            <p>{a[4]}</p>
          </div>)
            :
            (<div class="mt-2 flex items-center justify-center bg-blue-500 text-white text-sm font-bold px-4 py-3" role="alert">
            <p>Implements {a[4].toString()}</p>
          </div>)
            
            }
            </div>):(<div></div>)
            }       

            <VariablesTable vdata={a[1]}/>
            <MethodsTable mdata={a[2]}/>
            
        </div> ))}
        </div>
        </div>
    )
}

export default TableData
